"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { PhoneState, Notification } from "@/components/GameInterface";
import { SettingsApp } from "@/components/apps/SettingsApp";
import { FileManagerApp } from "@/components/apps/FileManagerApp";
import { TerminalApp } from "@/components/apps/TerminalApp";
import { AntivirusApp } from "@/components/apps/AntivirusApp";
import { StatusApp } from "@/components/apps/StatusApp";
import { MessagesApp } from "@/components/apps/MessagesApp";
import { BatteryApp } from "@/components/apps/BatteryApp";
import { SecurityApp } from "@/components/apps/SecurityApp";

interface PhoneScreenProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  isPlayer: boolean;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  showTutorial: boolean;
  onSkipTutorial: () => void;
  notifications?: Notification[];
  onBack?: () => void;
  enemyHealth?: number;
}

interface App {
  id: string;
  name: string;
  icon: string;
  color: string;
  position: { x: number; y: number };
  component?: React.ComponentType<any>;
}

export function PhoneScreen({ 
  phoneState, 
  setPhoneState, 
  isPlayer, 
  onAttack, 
  onDefend,
  showTutorial,
  onSkipTutorial,
  notifications = [],
  onBack,
  enemyHealth = 100
}: PhoneScreenProps) {
  const [openApp, setOpenApp] = useState<string | null>(null);
  const [apps, setApps] = useState<App[]>([]);
  const [showNotifications, setShowNotifications] = useState(false);
  const [notificationPanelY, setNotificationPanelY] = useState(-100);

  // Initialize apps with random positions
  useEffect(() => {
    const baseApps: Omit<App, 'position'>[] = [
      { id: 'settings', name: 'Settings', icon: '‚öôÔ∏è', color: 'bg-gray-600', component: SettingsApp },
      { id: 'files', name: 'Files', icon: 'üìÅ', color: 'bg-blue-600', component: FileManagerApp },
      { id: 'terminal', name: 'Terminal', icon: 'üíª', color: 'bg-black', component: TerminalApp },
      { id: 'antivirus', name: 'Antivirus', icon: 'üõ°Ô∏è', color: 'bg-green-600', component: AntivirusApp },
      { id: 'status', name: 'Status', icon: 'üìä', color: 'bg-purple-600', component: StatusApp },
      { id: 'messages', name: 'Messages', icon: 'üí¨', color: 'bg-blue-500', component: MessagesApp },
      { id: 'battery', name: 'Battery', icon: 'üîã', color: 'bg-yellow-600', component: BatteryApp },
      { id: 'security', name: 'Security', icon: 'üîí', color: 'bg-red-600', component: SecurityApp },
    ];

    // Generate random grid positions (4x6 grid)
    const positions = [];
    for (let row = 0; row < 6; row++) {
      for (let col = 0; col < 4; col++) {
        positions.push({ x: col, y: row });
      }
    }
    
    // Shuffle positions
    const shuffledPositions = positions.sort(() => Math.random() - 0.5);
    
    const appsWithPositions = baseApps.map((app, index) => ({
      ...app,
      position: shuffledPositions[index]
    }));

    setApps(appsWithPositions);
  }, []);

  const handleAppClick = (appId: string) => {
    if (!isPlayer) return;
    setOpenApp(appId);
  };

  const closeApp = () => {
    setOpenApp(null);
  };

  const toggleNotifications = () => {
    setShowNotifications(!showNotifications);
    setNotificationPanelY(showNotifications ? -100 : 0);
  };

  const currentApp = apps.find(app => app.id === openApp);

  return (
    <div className="h-screen w-screen bg-gradient-to-br from-blue-900 via-purple-900 to-pink-900 relative overflow-hidden">
      {/* Status Bar */}
      <div className="flex justify-between items-center p-4 text-white text-sm relative z-20">
        <div className="flex items-center space-x-2">
          <div className="w-4 h-2 bg-white rounded-sm"></div>
          <span className="text-xs">Sabotage OS</span>
        </div>
        <div className="flex items-center space-x-2">
          {/* Enemy Health Indicator */}
          {isPlayer && (
            <div className="flex items-center space-x-1">
              <span className="text-xs">Enemy:</span>
              <div className="w-8 h-2 bg-gray-600 rounded-full">
                <div 
                  className="h-full bg-red-500 rounded-full transition-all duration-300"
                  style={{ width: `${enemyHealth}%` }}
                ></div>
              </div>
            </div>
          )}
          <span className="text-xs">{phoneState.battery}%</span>
          <div className={`w-6 h-3 border border-white rounded-sm ${
            phoneState.battery > 20 ? 'bg-green-500' : 'bg-red-500'
          }`}></div>
        </div>
      </div>

      {/* Notification Panel */}
      <div 
        className={`absolute top-0 left-0 right-0 bg-black/90 backdrop-blur-lg z-30 transition-transform duration-300 ${
          showNotifications ? 'translate-y-0' : '-translate-y-full'
        }`}
        style={{ height: '60%' }}
      >
        <div className="p-4 pt-16">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-white font-semibold text-lg">Notifications</h3>
            <Button 
              onClick={toggleNotifications}
              variant="ghost"
              size="sm"
              className="text-white hover:bg-white/20"
            >
              ‚úï
            </Button>
          </div>
          <div className="space-y-3 max-h-96 overflow-y-auto">
            {notifications.map((notification) => (
              <div key={notification.id} className="bg-white/10 rounded-2xl p-4 border border-white/20">
                <div className="flex items-start space-x-3">
                  <span className="text-2xl">{notification.icon}</span>
                  <div className="flex-1">
                    <div className="text-white font-medium">{notification.title}</div>
                    <div className="text-gray-300 text-sm">{notification.message}</div>
                    <div className="text-gray-400 text-xs mt-1">
                      {new Date(notification.timestamp).toLocaleTimeString()}
                    </div>
                  </div>
                </div>
              </div>
            ))}
            {notifications.length === 0 && (
              <div className="text-center text-gray-400 py-8">
                <div className="text-4xl mb-2">üîî</div>
                <div>No notifications</div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Swipe Down Area */}
      <div 
        className="absolute top-0 left-0 right-0 h-16 z-20 cursor-pointer"
        onClick={toggleNotifications}
      >
        <div className="w-full h-full flex items-center justify-center">
          <div className="w-12 h-1 bg-white/30 rounded-full"></div>
        </div>
      </div>

      {/* Health Bar */}
      <div className="px-4 mb-4 relative z-10">
        <div className="bg-black/30 rounded-full h-3">
          <div 
            className={`h-full rounded-full transition-all duration-300 ${
              phoneState.health > 50 ? 'bg-green-500' : phoneState.health > 25 ? 'bg-yellow-500' : 'bg-red-500'
            }`}
            style={{ width: `${phoneState.health}%` }}
          ></div>
        </div>
        <p className="text-white text-sm text-center mt-1">System Health: {phoneState.health}%</p>
      </div>

      {/* Main Content */}
      <div className="flex-1 relative z-10">
        {openApp && currentApp?.component ? (
          <div className="h-full p-4">
            <div className="bg-black/80 backdrop-blur-lg rounded-2xl h-full p-4">
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-white font-semibold">{currentApp.name}</h3>
                <div className="flex space-x-2">
                  {onBack && (
                    <Button 
                      onClick={onBack}
                      variant="ghost"
                      size="sm"
                      className="text-white hover:bg-white/20"
                    >
                      üè†
                    </Button>
                  )}
                  <Button 
                    onClick={closeApp}
                    variant="ghost"
                    size="sm"
                    className="text-white hover:bg-white/20"
                  >
                    ‚úï
                  </Button>
                </div>
              </div>
              <currentApp.component 
                phoneState={phoneState}
                setPhoneState={setPhoneState}
                onAttack={onAttack}
                onDefend={onDefend}
                isPlayer={isPlayer}
              />
            </div>
          </div>
        ) : (
          /* Home Screen with Apps */
          <div className="h-full p-6">
            {/* Time Display */}
            <div className="text-center text-white mb-8">
              <div className="text-4xl font-thin mb-2">
                {new Date().toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'})}
              </div>
              <div className="text-sm opacity-80">
                {new Date().toLocaleDateString([], {weekday: 'long', month: 'short', day: 'numeric'})}
              </div>
            </div>

            {/* Apps Grid */}
            <div className="grid grid-cols-4 gap-6 flex-1 content-center">
              {apps.map((app) => (
                <div
                  key={app.id}
                  className="flex flex-col items-center cursor-pointer transform hover:scale-105 transition-transform duration-200"
                  onClick={() => handleAppClick(app.id)}
                >
                  <div className={`w-16 h-16 ${app.color} rounded-2xl flex items-center justify-center text-white text-2xl shadow-lg mb-2 ${!isPlayer ? 'opacity-50' : ''}`}>
                    {app.icon}
                  </div>
                  <span className="text-white text-xs text-center font-medium">{app.name}</span>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Visual Effects */}
      {phoneState.virusCount > 0 && (
        <div className="absolute inset-0 pointer-events-none z-40">
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-red-500 text-8xl animate-pulse">
            ‚ö†Ô∏è
          </div>
        </div>
      )}

      {phoneState.health < 25 && (
        <div className="absolute inset-0 bg-red-500/20 pointer-events-none animate-pulse z-30"></div>
      )}

      {/* Notification Badge */}
      {notifications.length > 0 && !showNotifications && (
        <div className="absolute top-2 right-2 bg-red-500 text-white text-xs rounded-full w-6 h-6 flex items-center justify-center font-bold z-30 animate-bounce">
          {notifications.length > 9 ? '9+' : notifications.length}
        </div>
      )}
    </div>
  );
}
