"use client";

import { useState, useEffect, useCallback } from "react";
import { Button } from "@/components/ui/button";
import { PhoneScreen } from "@/components/PhoneScreen";
import { BotAI } from "@/components/BotAI";

interface GameInterfaceProps {
  onBack: () => void;
  onWin: (earnedGB: number) => void;
  showTutorial: boolean;
  onSkipTutorial: () => void;
}

export interface PhoneState {
  battery: number;
  health: number;
  cpuUsage: number;
  ramUsage: number;
  filesDeleted: number;
  virusCount: number;
  antivirusActive: boolean;
  firewallActive: boolean;
}

export interface AttackLog {
  id: string;
  type: string;
  damage: number;
  timestamp: number;
  source: 'player' | 'bot';
}

export interface Notification {
  id: string;
  title: string;
  message: string;
  timestamp: number;
  type: 'attack' | 'defense' | 'system';
  icon: string;
}

export function GameInterface({ onBack, onWin, showTutorial, onSkipTutorial }: GameInterfaceProps) {
  const [playerPhone, setPlayerPhone] = useState<PhoneState>({
    battery: 100,
    health: 100,
    cpuUsage: 20,
    ramUsage: 30,
    filesDeleted: 0,
    virusCount: 0,
    antivirusActive: false,
    firewallActive: false,
  });

  const [botPhone, setBotPhone] = useState<PhoneState>({
    battery: 100,
    health: 100,
    cpuUsage: 25,
    ramUsage: 35,
    filesDeleted: 0,
    virusCount: 0,
    antivirusActive: false,
    firewallActive: false,
  });

  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [gameOver, setGameOver] = useState(false);
  const [winner, setWinner] = useState<'player' | 'bot' | null>(null);
  const [attackCount, setAttackCount] = useState(0);
  const [currentView, setCurrentView] = useState<'player' | 'bot'>('player');

  // Check for game over conditions
  useEffect(() => {
    if (playerPhone.health <= 0 || playerPhone.battery <= 0) {
      setGameOver(true);
      setWinner('bot');
    } else if (botPhone.health <= 0 || botPhone.battery <= 0) {
      setGameOver(true);
      setWinner('player');
      const earnedGB = Math.floor(attackCount * 2.5 + 50);
      setTimeout(() => onWin(earnedGB), 2000);
    }
  }, [playerPhone.health, playerPhone.battery, botPhone.health, botPhone.battery, attackCount, onWin]);

  const addNotification = useCallback((title: string, message: string, type: 'attack' | 'defense' | 'system', icon: string) => {
    const notification: Notification = {
      id: Date.now().toString(),
      title,
      message,
      timestamp: Date.now(),
      type,
      icon
    };
    setNotifications(prev => [notification, ...prev.slice(0, 19)]); // Keep last 20 notifications
  }, []);

  const attackBot = useCallback((attackType: string, damage: number) => {
    if (gameOver) return;
    
    setBotPhone(prev => {
      const newHealth = Math.max(0, prev.health - damage);
      const newBattery = attackType === 'battery_drain' ? Math.max(0, prev.battery - damage) : prev.battery;
      const newVirusCount = attackType === 'virus' ? prev.virusCount + 1 : prev.virusCount;
      const newFilesDeleted = attackType === 'file_delete' ? prev.filesDeleted + 1 : prev.filesDeleted;
      
      return {
        ...prev,
        health: newHealth,
        battery: newBattery,
        virusCount: newVirusCount,
        filesDeleted: newFilesDeleted,
        cpuUsage: Math.min(100, prev.cpuUsage + (attackType === 'cpu_overload' ? damage : 0)),
        ramUsage: Math.min(100, prev.ramUsage + (attackType === 'ram_flood' ? damage : 0))
      };
    });
    
    setAttackCount(prev => prev + 1);
    addNotification(
      "Attack Successful", 
      `${attackType.replace('_', ' ')} dealt ${damage} damage to enemy`, 
      'attack', 
      '‚öîÔ∏è'
    );
  }, [gameOver, addNotification]);

  const defendPlayer = useCallback((defenseType: string) => {
    if (gameOver) return;
    
    setPlayerPhone(prev => {
      switch (defenseType) {
        case 'antivirus':
          addNotification("Defense Activated", "Antivirus protection enabled", 'defense', 'üõ°Ô∏è');
          return { ...prev, antivirusActive: true, virusCount: 0 };
        case 'firewall':
          addNotification("Defense Activated", "Firewall protection enabled", 'defense', 'üî•');
          return { ...prev, firewallActive: true };
        case 'repair':
          addNotification("System Repair", "Health restored by 20 points", 'defense', 'üîß');
          return { ...prev, health: Math.min(100, prev.health + 20) };
        case 'battery_saver':
          addNotification("Power Management", "Battery optimized", 'defense', 'üîã');
          return { ...prev, battery: Math.min(100, prev.battery + 15) };
        default:
          return prev;
      }
    });
  }, [gameOver, addNotification]);

  if (gameOver) {
    return (
      <div className="h-screen w-screen bg-gradient-to-br from-blue-900 via-purple-900 to-pink-900 flex flex-col justify-center items-center">
        <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-8 max-w-sm w-full mx-4 text-center border border-white/20">
          <div className="text-6xl mb-4">
            {winner === 'player' ? 'üèÜ' : 'üíÄ'}
          </div>
          <h2 className={`text-3xl font-bold mb-4 ${winner === 'player' ? 'text-green-400' : 'text-red-400'}`}>
            {winner === 'player' ? 'VICTORY!' : 'DEFEAT!'}
          </h2>
          <p className="text-white mb-6">
            {winner === 'player' 
              ? `You earned ${Math.floor(attackCount * 2.5 + 50)} GB!` 
              : 'Your phone was destroyed!'
            }
          </p>
          <Button 
            onClick={onBack}
            className="w-full bg-blue-600 hover:bg-blue-700 text-white py-4 text-lg font-semibold rounded-2xl"
          >
            Return to Menu
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="h-screen w-screen bg-black overflow-hidden">
      {/* Current Phone View */}
      <PhoneScreen 
        phoneState={currentView === 'player' ? playerPhone : botPhone}
        setPhoneState={currentView === 'player' ? setPlayerPhone : setBotPhone}
        isPlayer={currentView === 'player'}
        onAttack={attackBot}
        onDefend={defendPlayer}
        showTutorial={showTutorial}
        onSkipTutorial={onSkipTutorial}
        notifications={notifications}
        onBack={onBack}
        enemyHealth={currentView === 'player' ? botPhone.health : playerPhone.health}
      />

      {/* Phone Switcher Button */}
      <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2">
        <button
          onClick={() => setCurrentView(currentView === 'player' ? 'bot' : 'player')}
          className="bg-black/80 backdrop-blur-lg border-2 border-white/30 rounded-full p-4 shadow-lg hover:scale-105 transition-transform duration-200"
        >
          <div className="flex items-center space-x-2">
            <div className={`w-3 h-3 rounded-full ${currentView === 'player' ? 'bg-green-500' : 'bg-gray-400'}`}></div>
            <span className="text-white text-sm font-medium">
              {currentView === 'player' ? 'Your Phone' : 'Enemy Phone'}
            </span>
            <div className={`w-3 h-3 rounded-full ${currentView === 'bot' ? 'bg-red-500' : 'bg-gray-400'}`}></div>
          </div>
        </button>
      </div>

      <BotAI 
        playerPhone={playerPhone}
        setPlayerPhone={setPlayerPhone}
        addNotification={addNotification}
        gameOver={gameOver}
      />
    </div>
  );
}
