"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";

interface ShopProps {
  onBack: () => void;
  gigabytes: number;
  setGigabytes: React.Dispatch<React.SetStateAction<number>>;
}

interface ShopItem {
  id: string;
  name: string;
  description: string;
  price: number;
  icon: string;
  category: 'defense' | 'attack' | 'utility';
  owned: boolean;
}

export function Shop({ onBack, gigabytes, setGigabytes }: ShopProps) {
  const [selectedCategory, setSelectedCategory] = useState<'all' | 'defense' | 'attack' | 'utility'>('all');
  const [ownedItems, setOwnedItems] = useState<Set<string>>(new Set());

  const shopItems: ShopItem[] = [
    // Defense Items
    {
      id: 'premium_antivirus',
      name: 'Premium Antivirus',
      description: 'Advanced virus protection with real-time scanning',
      price: 150,
      icon: 'üõ°Ô∏è',
      category: 'defense',
      owned: ownedItems.has('premium_antivirus')
    },
    {
      id: 'military_firewall',
      name: 'Military Firewall',
      description: 'Military-grade firewall protection',
      price: 200,
      icon: 'üî•',
      category: 'defense',
      owned: ownedItems.has('military_firewall')
    },
    {
      id: 'auto_repair',
      name: 'Auto Repair System',
      description: 'Automatically repairs system damage',
      price: 180,
      icon: 'üîß',
      category: 'defense',
      owned: ownedItems.has('auto_repair')
    },
    {
      id: 'backup_shield',
      name: 'Backup Shield',
      description: 'Creates automatic backups during attacks',
      price: 120,
      icon: 'üíæ',
      category: 'defense',
      owned: ownedItems.has('backup_shield')
    },
    
    // Attack Items
    {
      id: 'mega_virus',
      name: 'Mega Virus',
      description: 'Devastating virus that deals massive damage',
      price: 250,
      icon: 'üí£',
      category: 'attack',
      owned: ownedItems.has('mega_virus')
    },
    {
      id: 'quantum_ddos',
      name: 'Quantum DDoS',
      description: 'Next-gen DDoS attack with quantum processing',
      price: 300,
      icon: '‚ö°',
      category: 'attack',
      owned: ownedItems.has('quantum_ddos')
    },
    {
      id: 'data_shredder',
      name: 'Data Shredder',
      description: 'Permanently destroys enemy files',
      price: 220,
      icon: 'üóëÔ∏è',
      category: 'attack',
      owned: ownedItems.has('data_shredder')
    },
    {
      id: 'logic_bomb',
      name: 'Logic Bomb',
      description: 'Delayed attack that triggers automatically',
      price: 280,
      icon: 'üí•',
      category: 'attack',
      owned: ownedItems.has('logic_bomb')
    },
    
    // Utility Items
    {
      id: 'battery_booster',
      name: 'Battery Booster',
      description: 'Increases maximum battery capacity',
      price: 100,
      icon: 'üîã',
      category: 'utility',
      owned: ownedItems.has('battery_booster')
    },
    {
      id: 'cpu_optimizer',
      name: 'CPU Optimizer',
      description: 'Reduces CPU usage and improves performance',
      price: 130,
      icon: '‚öôÔ∏è',
      category: 'utility',
      owned: ownedItems.has('cpu_optimizer')
    },
    {
      id: 'ram_expander',
      name: 'RAM Expander',
      description: 'Increases available memory',
      price: 110,
      icon: 'üíø',
      category: 'utility',
      owned: ownedItems.has('ram_expander')
    },
    {
      id: 'speed_hack',
      name: 'Speed Hack',
      description: 'Reduces attack cooldowns',
      price: 160,
      icon: 'üöÄ',
      category: 'utility',
      owned: ownedItems.has('speed_hack')
    }
  ];

  const filteredItems = shopItems.filter(item => 
    selectedCategory === 'all' || item.category === selectedCategory
  );

  const purchaseItem = (item: ShopItem) => {
    if (gigabytes >= item.price && !ownedItems.has(item.id)) {
      setGigabytes(prev => prev - item.price);
      setOwnedItems(prev => new Set([...prev, item.id]));
    }
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'defense': return 'text-green-400';
      case 'attack': return 'text-red-400';
      case 'utility': return 'text-blue-400';
      default: return 'text-gray-400';
    }
  };

  return (
    <div className="h-screen w-screen bg-gradient-to-br from-blue-900 via-purple-900 to-pink-900 flex flex-col">
      {/* Status Bar */}
      <div className="flex justify-between items-center p-4 text-white text-sm">
        <div className="flex items-center space-x-2">
          <div className="w-4 h-2 bg-white rounded-sm"></div>
          <span className="text-xs">Sabotage OS</span>
        </div>
        <div className="flex items-center space-x-1">
          <span className="text-xs">100%</span>
          <div className="w-6 h-3 border border-white rounded-sm bg-green-500"></div>
        </div>
      </div>

      {/* Header */}
      <div className="p-4">
        <div className="bg-black/30 backdrop-blur-lg rounded-3xl p-4 border border-white/20">
          <div className="flex justify-between items-center mb-3">
            <div className="flex items-center space-x-3">
              <div className="w-12 h-12 bg-blue-600 rounded-2xl flex items-center justify-center text-white text-2xl">
                üõí
              </div>
              <div>
                <h1 className="text-xl font-bold text-white">SHOP</h1>
                <p className="text-white/60 text-sm">Upgrade Arsenal</p>
              </div>
            </div>
            <Button 
              onClick={onBack}
              variant="ghost"
              className="text-white hover:bg-white/20 rounded-2xl"
            >
              üè†
            </Button>
          </div>
          
          <div className="bg-black/40 rounded-2xl p-3 text-center">
            <p className="text-green-400 font-mono text-xl font-bold">{gigabytes} GB</p>
            <p className="text-white/80 text-xs">Available Currency</p>
          </div>
        </div>
      </div>

      {/* Category Filter */}
      <div className="px-4 mb-4">
        <div className="bg-black/30 backdrop-blur-lg rounded-2xl p-3 border border-white/20">
          <div className="grid grid-cols-4 gap-2">
            {['all', 'defense', 'attack', 'utility'].map((category) => (
              <Button
                key={category}
                onClick={() => setSelectedCategory(category as any)}
                className={`${
                  selectedCategory === category 
                    ? 'bg-blue-600 hover:bg-blue-700' 
                    : 'bg-white/20 hover:bg-white/30'
                } text-white capitalize text-xs py-2 rounded-xl`}
              >
                {category}
              </Button>
            ))}
          </div>
        </div>
      </div>

      {/* Shop Items */}
      <div className="flex-1 px-4 pb-4 overflow-y-auto">
        <div className="grid grid-cols-2 gap-3">
          {filteredItems.map((item) => (
            <div key={item.id} className="bg-black/30 backdrop-blur-lg rounded-2xl p-4 border border-white/20">
              <div className="text-center mb-3">
                <div className="text-3xl mb-2">{item.icon}</div>
                <h3 className="text-white font-semibold text-sm">{item.name}</h3>
                <p className={`text-xs capitalize ${getCategoryColor(item.category)}`}>
                  {item.category}
                </p>
              </div>
              
              <p className="text-gray-300 text-xs mb-3 text-center leading-relaxed">
                {item.description}
              </p>
              
              <div className="text-center mb-3">
                <span className="text-green-400 font-mono text-sm font-bold">{item.price} GB</span>
              </div>
              
              <Button
                onClick={() => purchaseItem(item)}
                disabled={gigabytes < item.price || ownedItems.has(item.id)}
                className={`w-full text-xs py-2 rounded-xl ${
                  ownedItems.has(item.id)
                    ? 'bg-green-600 hover:bg-green-600 cursor-not-allowed'
                    : gigabytes >= item.price
                      ? 'bg-blue-600 hover:bg-blue-700'
                      : 'bg-gray-600 hover:bg-gray-600 cursor-not-allowed'
                } text-white font-semibold`}
              >
                {ownedItems.has(item.id) ? 'OWNED' : 'BUY'}
              </Button>
            </div>
          ))}
        </div>

        {/* Owned Items Summary */}
        {ownedItems.size > 0 && (
          <div className="bg-black/30 backdrop-blur-lg rounded-2xl p-4 mt-4 border border-white/20">
            <h2 className="text-white font-semibold mb-3 text-center">Your Arsenal</h2>
            <div className="grid grid-cols-4 gap-3">
              {Array.from(ownedItems).map((itemId) => {
                const item = shopItems.find(i => i.id === itemId);
                if (!item) return null;
                
                return (
                  <div key={itemId} className="bg-white/10 rounded-xl p-2 text-center">
                    <div className="text-lg mb-1">{item.icon}</div>
                    <div className="text-white text-xs font-medium">{item.name}</div>
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
